@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap);
.header {
  height: 8%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#animateText {
  margin: 0.5%;
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: xx-large;
  letter-spacing: 1vw;
  background: linear-gradient(90deg, #000, #fff, #000);
  background-repeat: no-repeat;
  background-size: 80%;
  animation: animate 3s linear infinite;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(255, 255, 255, 0);
}

@media only screen and (max-width: 510px) {
  #animateText {
    font-size: large;
  }
}

@media only screen and (max-width: 320px) {
  #animateText {
    font-size: medium;
  }
}

@keyframes animate {
  0% {
    background-position: -500%;
  }
  100% {
    background-position: 500%;
  }
}

.buttons-bar {
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  height: 70%;
  margin-left: 2%;
  margin-right: 2%;
  padding-left: 2%;
  padding-right: 2%;
  border-radius: 50vw;
  font-size: medium;
  color: white;
  background-color: black;
  transition: 0.3s;
}

@media only screen and (max-width: 510px) {
  button {
    font-size: small;
  }
}

@media only screen and (max-width: 410px) {
  button {
    font-size: x-small;
  }
}

@media only screen and (max-width: 320px) {
  button {
    font-size: xx-small;
  }
}

button:hover {
  font-style: bold;
  border-width: 0.3vh;
  cursor: pointer;
  transition: 0.3s;
}

.buttonStyle1:hover {
  border-color: green;
  color: green;
}

.buttonStyle2:hover {
  border-color: peru;
  color: peru;
}

#reset {
  opacity: 0.6;
  background-color: orangered;
}
#reset:hover {
  opacity: 0.9;
}

button:disabled,
.buttonStyle1:disabled,
.buttonStyle2:disabled,
#reset:disabled {
  background-color: grey;
  color: white;
  border-width: 0vw;
  opacity: 1;
  cursor: not-allowed;
}

.bar-container {
  width: 100%;
  height: 74%;
  margin-left: -1%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bar {
  width: 4vw;
  height: 70vh;
  margin: 0 1.5%;
  transform-style: preserve-3d;
  transform: rotateX(-20deg) rotateY(-30deg);
  background-color: rgba(254, 254, 254, 0.25);
}

.side {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(254, 254, 254, 0.25);
}

.back {
  transform: translateZ(-4vw);
}

.left {
  transform-origin: left;
  transform: rotateY(90deg);
}

.right {
  transform-origin: right;
  transform: rotateY(-90deg);
}

.top {
  height: 4vw;
  transform-origin: top;
  transform: rotateX(-90deg);
  background-color: rgba(254, 254, 254, 0.15);
}

.bottom {
  height: 4vw;
  transform-origin: top;
  transform: translateY(70vh) rotateX(-90deg);
  background-color: rgba(225, 0, 120, 0.5);
  box-shadow: 5px 5px 50px 5px rgba(225, 0, 120, 0.2);
  transition: 0.3s;
}

.color-bar {
  background-color: rgba(225, 0, 120, 0.5);
  box-shadow: 5px 5px 50px 5px rgba(225, 0, 120, 0.2);
  transition: 0.3s;
}

#text-array-size {
  color: white;
}

@media only screen and (max-width: 820px) {
  #text-array-size {
    font-size: small;
  }
}

@media only screen and (max-width: 510px) {
  #text-array-size {
    font-size: x-small;
  }
}

@media only screen and (max-width: 320px) {
  #text-array-size {
    font-size: xx-small;
  }
}

#text-animation-speed {
  color: white;
}

@media only screen and (max-width: 820px) {
  #text-animation-speed {
    font-size: small;
  }
}

@media only screen and (max-width: 510px) {
  #text-animation-speed {
    font-size: x-small;
  }
}

@media only screen and (max-width: 320px) {
  #text-animation-speed {
    font-size: xx-small;
  }
}

.range-slider-container {
  width: 100%;
  margin-bottom: 2%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 50vh;
  background-image: linear-gradient(
    to bottom,
    rgba(100, 100, 250, 0.9),
    rgba(100, 100, 250, 0.5),
    rgba(100, 100, 250, 0.9)
  );
  opacity: 0.9;
}

.range-slider-container:hover {
  opacity: 1;
}

#range-slider {
  height: 8%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  transition: visibility 0.3s linear, opacity 0.3s linear;
}

.column {
  width: 80%;
  /* align-self: center; */
  justify-self: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  align-items: center;
  font-family: "JetBrains Mono", monospace;
}

.main-container {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
  background-image: linear-gradient(
    to bottom right,
    #265077,
    #022140,
    #494b68,
    #1e4258,
    #2d575d
  );
}

